<!--
  @auth:lcw
  echarts
-->
<template>
  <div
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
    ref="chartEl"
    :style="{width : params.canvasStyle.width, height: params.canvasStyle.height}"
  ></div>
</template>

<script>
  import * as echarts from "echarts";
  import "echarts-wordcloud/dist/echarts-wordcloud";
  import "echarts-wordcloud/dist/echarts-wordcloud.min";

  export default {
    name: "wordcloud",
    props: {
      params: {
        type: Object,
        default: {
          canvasStyle: {
            width: "1000px",
            height: "600px"
          },
          data: [],
          options: {}
        }
      }
    },
    data() {
      return {
        myChart: null,
      };
    },
    mounted() {
      this.init();
      this.$once('hook:beforeDestroy', () => {
        if (this.interval) {
          clearInterval(this.interval);
        }
        this.myChart && this.myChart.dispose();
      })
    },
    methods: {
      init() {
        if (this.myChart) {
          this.myChart.dispose();
          this.myChart = null;
        }
        if (this.params.data.length === 0) return;
        let propData = [...this.params.data];
        let options = this.params.options;

        this.myChart = echarts.init(this.$refs.chartEl);
        this.option = {
          series: [
            {
              type: "wordCloud",
              //
              gridSize: 16,
              //
              sizeRange: [10, 30],
              //[0,0]--
              // rotationRange: [-45, 0, 45, 90],
              // rotationRange: [ 0,90],
              rotationRange: [0, 0],
              //
              // maskImage: maskImage,
              textStyle: {
                normal: {
                  color:"#c8f5ff"
                },
                emphasis:{
                  color:"#f2684f",
                  fontWeight:"bold"
                }
              },
              //
              left: "center",
              top: "center",
              right: null,
              bottom: null,
              width: "200%",
              height: "200%",
              //
              data: propData
            }
          ]
        };
        this.myChart.setOption(this.option);

        let clickCurIndex = -1;
        if(options.isClick){
          this.myChart.on("click", params => {
            let name = params.name;
            if (clickCurIndex !== params.dataIndex) {
              // 
              this.myChart.dispatchAction({
                type: "downplay",
                dataIndex: clickCurIndex
              });
              // 
              this.myChart.dispatchAction({
                type: "highlight",
                dataIndex: params.dataIndex
              });
              clickCurIndex = params.dataIndex;
              name = params.name;
            } else {
              // 
              // 
              this.myChart.dispatchAction({
                type: "downplay",
                dataIndex: clickCurIndex
              });
              name = 'drop';
            }

            this.$emit("canvasClick", name);
          });
        }
      }
    },
    watch: {
      'params.data': {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      }
    }
  }
</script>

